<template>
  <div class="level">
    <div class="level_head relative">
      <img :src="levelBg">
      <div class="absolute all v_center">
        <div class="main_width white">
          <div class="level_title">ICO评级</div>
          <div class="level_text">最专业的独立ICO评级机构</div>
          <div class="level_input circle_5 flex">
            <input class="padding_10_15 white flex_1" type="" v-model="inputText">
            <img class="search_btn" :src="search_btn" @click="search">
          </div>
        </div>
      </div>
    </div>
    <div class="bg_light">
      <div class="main_width padding_30 lineheight_2">
        <div class="flex padding_v_10">
          <div>项目状态：</div>
          <div :class="[{'btn btn_blue': now.status===''}, 'margin_l_20 cursor']" @click="query('status')">全部</div>
          <div :class="[{'btn btn_blue': k===now.status}, 'margin_l_20 cursor']" v-for="(v,k) in status" @click="query('status', k)">{{ v }}</div>
        </div>
        <div class="flex padding_v_10">
          <div>评级分数：</div>
          <div :class="[{'btn btn_blue': now.score===''}, 'margin_l_20 cursor']" @click="query('score')">全部</div>
          <div :class="[{'btn btn_blue': k===now.score}, 'margin_l_20 cursor']" v-for="(v,k) in score" @click="query('score', k)">{{ v }}</div>
        </div>
        <div class="flex padding_v_10">
          <div>项目归属：</div>
          <div :class="[{'btn btn_blue': now.belong===''}, 'margin_l_20 cursor']" @click="query('belong')">全部</div>
          <div :class="[{'btn btn_blue': k===now.belong}, 'margin_l_20 cursor']" v-for="(v,k) in belong" @click="query('belong', k)">{{ v }}</div>
        </div>
      </div>
    </div>
    <div class="main_width padding_v_50 flex_row_4">
      <Project v-for="l,k in list" :project="l" :key="k"></Project>
    </div>
    <div class="flex_center padding_b_70">
      <div class="getmore bg_blue circle arrow_down cursor" @click="getmore"></div>
    </div>
  </div>
</template>

<script>
import api from '../api'
import Project from '@/components/Parts/Project'
export default {
  name: 'level',
  data () {
    return {
      levelBg: require('@/assets/images/level/banner.png'),
      search_btn: require('@/assets/images/level/search.png'),
      status: {0: '未开始', 1: '进行中', 2: '已结束'},
      score: {0: '0-2分', 2: '2-4分', 4: '4-6分', 6: '6-8分', 8: '8-10分'},
      belong: {0: '国内', 1: '国外'},
      now: {status: '', score: '', belong: ''},
      list: [],
      inputText: ''
    }
  },
  created () {
    api.post('score/project_list', {sign: 'token=0'}).then(res => {
      console.log(res)
      this.list = res
    })
  },
  methods: {
    getmore () {
      console.log(11)
    },
    query (t, n) {
      this.now[t] = n || ''
      var str = []
      for (let [k, v] of Object.entries(this.now)) {
        if (v) {
          str.push(k + '=' + v)
        }
      }
      str = str.join('&')
      str = str ? '&' + str : ''
      api.post('score/project_list', {sign: 'token=0' + str}).then(res => {
        console.log(res)
        this.list = res
      })
    },
    search () {
      if (!this.inputText) return false
      api.post('score/search', {sign: 'token=0&key=' + encodeURIComponent(this.inputText)}).then(res => {
        console.log(res)
        this.list = res
      })
    }
  },
  components: {
    Project
  }
}
</script>

<style type="text/css">
  .level_title{
    font-size: 46px;
  }
  .level_text{
    font-size: 20px;
    margin-bottom: 40px;
  }
  .level_input{
    width: 375px;
    border:1px solid #fff;
    background: rgba(255,255,255,0.10);
  }
  .search_btn{
    width: 24px;
    margin:9px;
    cursor: pointer;
  }
  .width_24{
    width:24px;
  }
  .arrow_down.getmore{
    width:40px;
    height: 40px
  }
  .arrow_down.getmore:before{
    top:13px;
    right: 14px;
    width: 10px;
    height: 10px;
    border-width: 2px;
    border-color: #fff
  }
</style>



